.exp {
  padding: 2em;
  margin-bottom: 30px !important;
  text-align: center;
  box-shadow: var(--shadow);
  transition: transform 0.2s linear;
}

/* .project:hover {
  transform: translateY(-7px);
} */

.project__description {
  margin-top: 1em;
}

.project__stack {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 1.2em 0;
}

.project__stack-item {
  margin: 0.5em;
  font-weight: 500;
  font-size: 0.8rem;
  color: var(--clr-fg-alt);
}

.project .link--icon {
  margin-left: 0.5em;
}

.skills__list1 {
  flex-wrap: wrap;
  justify-content: flex-start;
  width: 100%;
  display: flex;
}
